.custom-button {
    background-color: blue;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    /* autres styles personnalisés */
  }
  
  .button-step {
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    background-color: blue;
    color: white;
    transition: transform 0.3s, background-color 0.3s;
  }

  #next{
    color: green;
    font-size: 16px;

  }
  #prev{
    color: white;
    font-size: 16px;

  }
  #close{
    color: red;
    font-size: 16px;

  }
  
  .button-step:hover {
    transform: scale(1.1);
    background-color: darkblue;
  }
  
  body {
    margin: 0;
    padding: 0;
  }

  #div-rouge {
    position: absolute;
    top: 200px;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 200px;
    height: 200px;
    background-color: blue;
  }

